<template>
   <section class="flex flex-wrap justify-between">
      <div  class="w-full  md:w-1/2  self-center">
          <div class="content md:mt-10">
               <h1 class="lg:text-4xl md:text-2xl text-2xl md:text-left text-center font-normal mb-1">Hello, I'm</h1>
               <h1 class="lg:text-6xl md:text-5xl sm:text-5xl text-4xl md:text-left text-center font-medium mb-2"><span class="text-color font-bold">JAN <span class="primary-purple">EPHRAIM</span></span></h1>
               <h2 class="lg:text-2xl md:text-xl text-xl md:text-left text-center font-normal mb-5">Web Developer</h2>

               <p class="md:text-normal hidden lg:block md:text-justify text-center font-normal mb-6">
                  4 years of experience in Web Development. Experienced in building and maintaining web applications, designing user interfaces, 
                  and creating restful APIs.
               </p>

               <div class="md:text-left text-center mt-10 mb-5">
                  <ButtonUtils
                     @click="downloadCV"
                     btn_name="Download CV"
                     type="button"
                     class="py-3 mr-2 primary-purple-bg text-white rounded lg:w-3/12 w-5/12">
                        <template v-slot:right_icon>
                          <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" class="w-4 h-4 self-center ml-2">
                              <path stroke-linecap="round" stroke-linejoin="round" d="M3 16.5v2.25A2.25 2.25 0 005.25 21h13.5A2.25 2.25 0 0021 18.75V16.5M16.5 12L12 16.5m0 0L7.5 12m4.5 4.5V3" />
                           </svg>

                        </template>
                  </ButtonUtils>

                  <ButtonUtils
                     @click="navigateToPortfolio"
                     btn_name="My Portfolio"
                     type="button"
                     :class="[{'text-white btn-outlined-dark' : is_dark}]"
                     class="py-3 mr-2 btn-outlined md:inline-block hidden rounded lg:w-3/12 md:w-5/12">
                        <template v-slot:right_icon>
                           <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" class="w-4 h-4 self-center ml-2">
                              <path stroke-linecap="round" stroke-linejoin="round" d="M8.25 4.5l7.5 7.5-7.5 7.5" />
                           </svg>

                        </template>
                  </ButtonUtils>
               </div>

               <SocialLinks class="md:text-left text-center"/>

               <div class="md:hidden block text-center" style="position:absolute; bottom:10%; left:0; right:0">
                  <button @click="navigateToPortfolio" class="animate-bounce">
                     <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" class="w-10 h-10">
                        <path stroke-linecap="round" stroke-linejoin="round" d="M19.5 5.25l-7.5 7.5-7.5-7.5m15 6l-7.5 7.5-7.5-7.5" />
                     </svg>
                  </button>
               </div>
              
         </div>
      </div>

      <div class=" lg:w-5/12 md:w-1/2 md:block hidden self-center">
         <div class="img self-center">
               <img src="../assets/profile.png" class="shape" alt="my profile"/>
            </div>
      </div>
   </section>


</template>

<script>
import pdf from '../assets/Jan-Ephraim-Agbayani-CV.pdf'
import SocialLinks from '@/components/Utilities/SocialLinks.vue'
import ButtonUtils from '../components/Utilities/ButtonUtils.vue'

export default {
   name: 'LandingPage',

   components: {
      SocialLinks,
      ButtonUtils
   },

   inject:['is_dark'],

   methods: {
      navigateToPortfolio() {
         const element = document.querySelector('#portfolio');
         element.scrollIntoView({ behavior: 'smooth' });
      },

      downloadCV() {
         const url = pdf
         const link = document.createElement('a');
         link.href = url;
         link.download = 'Jan Ephraim Agbayani CV.pdf';
         document.body.appendChild(link);
         link.click();
         document.body.removeChild(link);
      }
   }

}
</script>
