<template>
   <section :class="[{'md:h-screen flex landing-animation' : $route.path === '/about'}]" class="md:h-screen">
      <div class="self-center">
         <div class="header text-center w-full py-10">
            <PageHeader 
               title="About"
               subtitle="My Introduction"
            />
         </div>

         <div class="flex flex wrap">
            <div class="content md:px-10 px-0 flex flex-wrap justify-center">
               <div class="img-container w-10/12 flex flex-wrap">

                  <div class="image-container lg:w-5/12 md:w-6/12 md:inline-block hidden px-5 py-3">
                     <img src="../../assets/my-profile.jpg" class="rounded mx-auto shadow" style="width:80%" />   
                  </div>

                  <div class="content lg:w-7/12 md:w-6/12 w-full px-5 py-3">
                     <p class="text-justify">
                        {{about_myself}}
                     </p>

                     <div class="achievements flex flex-wrap justify-between my-6">
                        <ExperienceCounter
                           v-for="(item, index) in experience_data"
                           :key="index"
                           :counter="item.count"
                           :title="item.title"
                        />
                     </div>

                     <div class="flex flex-wrap lg:gap-4">
                        <ButtonUtils
                           @click="navigateTo('#contact')"
                           btn_name="Contact Me!"
                           type="button"
                           class="py-2 mr-2 primary-purple-bg text-white rounded lg:w-4/12  w-full mb-3">
                     
                        </ButtonUtils>

                        <ButtonUtils
                           @click="navigateTo('#qualifications')"
                           btn_name="More about myself"
                           type="button"
                           :class="[{'text-white btn-outlined-dark' : is_dark}]"
                           class="py-2 mr-2 btn-outlined  rounded lg:w-4/12 w-full mb-3">
                            
                        </ButtonUtils>
                     </div>

                  </div>
               </div>
            </div>
            
         </div>
      </div>
   </section>   
</template>

<script>
import PageHeader from  '../Utilities/PageHeader.vue'
import ExperienceCounter from '../Utilities/ExperienceCounter.vue'
import ButtonUtils from '../Utilities/ButtonUtils.vue'



export default {
   name: 'AboutPage',
   components: {
      PageHeader,
      ExperienceCounter,
      ButtonUtils
   },

   inject:['is_dark'],

    data() {
      return {
         about_myself: 'Web developer, experienced in building and maintaining web applications, designing user interfaces, and creating restful APIs. Experienced in working with projects using the VUE JS framework. Currently exploring to learn new technologies.',
         experience_data:[
            {
               count: 4,
               title: 'Years of experience'
            },
            {
               count: 4,
               title: 'Completed projects'
            },
            {
               count: 1,
               title: 'Companies worked'
            }
         ]
      }
    },

     methods: {
       navigateTo(selector) {
         if (selector === '#contact' && this.$route.fullPath === '/about') return this.$router.push('/contact')
         
         const element = document.querySelector(selector);
         return element.scrollIntoView({ behavior: 'smooth' });
        
      },
   }

}
</script>
