<template>
   <nav class="py-3 navbar w-full md:absolute">
      <div class="px-3 flex">
         <h1 class="text-xl title self-center mr-auto">Jan <span class="primary-purple">Ephraim</span>.</h1>
         <div class="self-center hidden sm:block" id="nav-content">
            <button 
               @click="navigateTo(nav.route)" 
               :key="index"
               :class="[{'active':  $route.fullPath === nav.route },'p-3']"
               v-for="(nav, index) in navigation_links" 
               >
                  {{nav.title}}
            </button>
         </div>

        <ThemeButton class="self-center"/>

         <div class="block sm:hidden">
            <button @click="openMenu(!toggle_menu)">
               <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" :class="['w-6 h-6 fill-current']">
                  <path stroke-linecap="round" stroke-linejoin="round" d="M3.75 6.75h16.5M3.75 12h16.5m-16.5 5.25h16.5" />
               </svg>
            </button>
         </div>
      </div>

      <CollapseMenu 
         v-show="toggle_menu" 
         @open-menu="openMenu"
         @navigate-to="navigateTo"
         :navigation_links="navigation_links"
         class="shadow-lg"
      />
   </nav>
</template>

<script>
import CollapseMenu from './CollapseMenu.vue'
import ThemeButton from './ThemeButton.vue'

export default {
   name: 'NavigationMenu',
   components: {
      CollapseMenu,
      ThemeButton
   },
   
   data() {
      return {
         toggle_menu: false,
         
         navigation_links:[
             {
               title:'Home',
               route: '/'
            },
            {
               title:'Portfolio',
               route: '/portfolio',
            },
            {
               title:'About',
               route: '/about'
            },
            {
               title:'Contact',
               route: '/contact'
            },
         ],
      }
   },

   methods:{
      openMenu(value) {
         return this.toggle_menu = value
      },

      navigateTo(route) {
         this.toggle_menu = false
         if (this.$route.fullPath === route) return
         this.$router.push(route)
      },
   }


}
</script>

<style scoped>
.active {
   border-bottom: 1px solid #6c56e1;
   color: #6c56e1;
}

.navbar {
   z-index: 1;
   animation: slideRight 1s ease forwards;
}

</style>