<template>
   <div class="px-0 text-center mt-2">
      <div
         v-for="(nav, index) in navigation_links" 
         :key="index"
         @click="navigateTo(nav.route)"
         :class="[{'active':  $route.fullPath === nav.route },'w-full cursor-pointer px-3 py-2 border-dark nav']"
         >
         {{nav.title}}
      </div>
   
   </div>
</template>

<script>
export default {
   name: 'ToggleMenu',
   props: {
      navigation_links: Array,
   },
   emits:['open-menu', 'navigate-to'],
   inject:['page_width'],

   mounted() {
      if(this.page_width === 639) this.$emit('open-menu', false)
   },

   watch: {
      page_width (value) {
         if (value <= 639) return this.$emit('open-menu', false)
         
      }
   },

   methods: {
      navigateTo(route) {
         this.$emit('navigate-to', route)
      }
   }
   
}
</script>

<style>
   .nav:hover {
      background:#6c56e1;
      color:white
   }

   
</style>
