<template>
  <section :class="[{'lg:h-screen flex landing-animation md:py-10' : $route.path === '/contact'}]">
   <div class="self-center w-full">
      <div class="header text-center w-full py-10">
         <PageHeader 
            title="Contact Me" 
            subtitle="Get in touch"
         />
      </div>


      <div class="content md:px-10 px-0 flex flex-wrap justify-center">
         <div class="img-container md:w-10/12  w-full flex flex-wrap">
            <div class="md:w-4/12 w-full justify-between p-5">

               <div class="contact-container mb-10">
                  <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" class="w-8 h-8  self-center primary-purple mb-2">
                     <path stroke-linecap="round" stroke-linejoin="round" d="M2.25 6.75c0 8.284 6.716 15 15 15h2.25a2.25 2.25 0 002.25-2.25v-1.372c0-.516-.351-.966-.852-1.091l-4.423-1.106c-.44-.11-.902.055-1.173.417l-.97 1.293c-.282.376-.769.542-1.21.38a12.035 12.035 0 01-7.143-7.143c-.162-.441.004-.928.38-1.21l1.293-.97c.363-.271.527-.734.417-1.173L6.963 3.102a1.125 1.125 0 00-1.091-.852H4.5A2.25 2.25 0 002.25 4.5v2.25z" />
                  </svg>
                  <h1 class="text-lg">Call Me</h1> 
                  <h2 >+63 926 894 2294</h2> 
               </div> 

               <div class="contact-container mb-10">
                  <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" class="w-8 h-8 self-center primary-purple mb-2">
                     <path stroke-linecap="round" stroke-linejoin="round" d="M21.75 6.75v10.5a2.25 2.25 0 01-2.25 2.25h-15a2.25 2.25 0 01-2.25-2.25V6.75m19.5 0A2.25 2.25 0 0019.5 4.5h-15a2.25 2.25 0 00-2.25 2.25m19.5 0v.243a2.25 2.25 0 01-1.07 1.916l-7.5 4.615a2.25 2.25 0 01-2.36 0L3.32 8.91a2.25 2.25 0 01-1.07-1.916V6.75" />
                  </svg>
                  <h1 class="text-lg">Email</h1> 
                  <h2>ephraimagbayani31@gmail.com</h2> 
               </div> 


            </div>

            <div class="lg:w-8/12 w-full p-5">
               <form @submit.prevent="submitContact">
                  <div 
                     :class="[{'bg-dark-field' : is_dark}]"
                     class="contact-container w-full flex flex-col pt-5 rounded mb-5 bg-light-field">
                     <label for="name" class="mb-1 text-sm px-5">Name</label>
                     <input 
                        v-model="name"
                        type="text"
                        name="name"
                        maxlength="30"
                        required
                        :class="[{'bg-dark-field' : is_dark}]"
                        class="input-field-dark px-5 pt-3 pb-5 bg-light-field" />
                  </div>

                  <div 
                     :class="[{'bg-dark-field' : is_dark}]"
                     class="contact-container w-full flex flex-col pt-5 rounded mb-5 bg-light-field">
                     <label for="email" class="mb-1 text-sm px-5">Email</label>
                     <input 
                        v-model="email"
                        type="email"
                        name="email"
                        maxlength="30"
                        required
                        :class="[{'bg-dark-field' : is_dark}]"
                        class="input-field-dark px-5 pt-3 pb-5 bg-light-field" />
                  </div>

                  <div 
                     :class="[{'bg-dark-field' : is_dark}]"
                     class="contact-container w-full flex flex-col pt-5 rounded mb-5 bg-light-field">
                     <label for="message" class="mb-1 text-sm px-5">Message</label>
                     <textarea
                        v-model="message"
                        maxlength="500"
                        required
                        name="message"
                        :class="[{'bg-dark-field' : is_dark}]"
                        class="input-field-dark px-5 pt-3 pb-5 h-32 bg-light-field"
                     >
                     </textarea>
                  </div>
                


                  <ButtonUtils
                     :loading="submit_loader"
                     :disabled="submit_disabled"
                     type="submit"
                     btn_name="Submit"
                     class="mr-1 rounded primary-purple-bg text-white p-3 md:w-3/12 w-full">
                        <template v-slot:right_icon>
                           <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5" stroke="currentColor" class="w-4 h-4 self-center ml-2">
                              <path stroke-linecap="round" stroke-linejoin="round" d="M6 12L3.269 3.126A59.768 59.768 0 0121.485 12 59.77 59.77 0 013.27 20.876L5.999 12zm0 0h7.5" />
                           </svg>

                        </template>
                  </ButtonUtils>
               </form>

               <h3 
                  v-show="show_message" 
                  :class="[{'text-red-600' : message_response === 'Failed to send the message'}]"
                  class="text-green-600 text-sm my-5">
                     {{message_response}}
               </h3>
            </div>
         </div>
      </div>
   </div>
  </section>
</template>

<script>
import PageHeader from '../components/Utilities/PageHeader.vue'
import ButtonUtils from '../components/Utilities/ButtonUtils.vue'
import axios from 'axios'

export default {
   name: 'QualificationPage',
   components: {
      PageHeader,
      ButtonUtils,
   },

   inject: ['is_dark'],

   data() {
      return {
         btn_utils_option:['primary-purple-bg'],
         name: '',
         email: '',
         message: '',
         submit_loader: false,
         submit_disabled: false,
         show_message: false,
         message_response: '',
      }
   },

   methods: {
      async submitContact() {
         this.submit_loader = true
         this.submit_disabled = true
        
         try {
            const is_successful = await axios.post('https://api.apispreadsheets.com/data/1lR5ScmwokCoy7vk/', JSON.stringify({
               name: this.name,
               email: this.email,
               message: this.message,
            }))

            this.submit_loader = false
            this.name = this.email = this.message = ''
   
            if (is_successful.status !== 201) return  this.showMessage('Failed to send the message')
             
            return this.showMessage('Message submitted successfully')

         } catch (err) {
            this.submit_loader = false
            this.showMessage('Failed to send the message')
         }
      },

      showMessage(data) {
         this.show_message = true
         this.message_response = data
         setTimeout(() => {
            this.submit_disabled = false
            this.show_message = false
            this.message_response = ''
         }, 5000)
      }
   }
}
</script>

<style scoped>
.contact-container {
   border-top-left-radius: 10px;
   border-top-right-radius: 10px;
}


.bg-light-field {
   background-color: #eeeffd;
}

.bg-dark-field {
   background-color: #242830;
   color: #fff;
}


input {
   border: none;
   outline: none;
}


input:focus {
   border-bottom:solid #3B82F6
}


textarea {
   border: none;
   outline: none;
}


textarea:focus {
   border-bottom:solid #3B82F6
}
</style>