<template>
  <div class="relative self-start">
    <div class="carousel">
      <div class="carousel-inner relative" :class="{ 'transition-transform duration-300 ease-out': shouldTransition }" :style="{ transform: `translateX(-${currentSlide * 100}%)` }">
        <slot />
      </div>
      <div class="absolute top-1/2 left-0 transform -translate-y-1/2 flex w-full justify-between px-4 z-10">
        <button @click="prevSlide" class="text-3xl primary-purple hover:text-gray-500 focus:outline-none">
          <svg xmlns="http://www.w3.org/2000/svg" class="h-8 w-8" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 19l-7-7 7-7" />
          </svg>
        </button>
        <button @click="nextSlide" class="text-3xl primary-purple hover:text-gray-500 focus:outline-none">
          <svg xmlns="http://www.w3.org/2000/svg" class="h-8 w-8" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 5l7 7-7 7" />
          </svg>
        </button>
      </div>
    </div>
    <div class="flex justify-center mt-4">
      <template v-for="(slide, index) in numSlides">
        <button :key="index" @click="goToSlide(index)" class="mx-2 text-2xl text-white focus:outline-none" :class="{ 'bg-gray-400': currentSlide !== index, 'primary-purple-bg': currentSlide === index }" style="width: 10px; height: 10px; border-radius: 50%"></button>
      </template>
    </div>
  </div>
</template>



<script>
export default {
  data() {
    return {
      currentSlide: 0,
      numSlides: 0,
      shouldTransition: false,
    };
  },
  mounted() {
    this.numSlides = this.$slots.default.filter(vnode => vnode.tag).length;
    this.startCarousel();
  },
  beforeDestroy() {
    this.stopCarousel();
  },
  methods: {
    startCarousel() {
      this.carouselInterval = setInterval(() => {
        this.nextSlide();
      }, 5000);
    },
    stopCarousel() {
      clearInterval(this.carouselInterval);
    },
    nextSlide() {
      this.shouldTransition = true;
      setTimeout(() => {
        this.currentSlide = (this.currentSlide + 1) % this.numSlides;
        this.shouldTransition = false;
      }, 300);
    },
    prevSlide() {
      this.shouldTransition = true;
      setTimeout(() => {
        this.currentSlide = (this.currentSlide - 1 + this.numSlides) % this.numSlides;
        this.shouldTransition = false;
      }, 300);
    },
    goToSlide(index) {
      if (index !== this.currentSlide) {
        this.shouldTransition = true;
        setTimeout(() => {
          this.currentSlide = index;
          this.shouldTransition = false;
        }, 300);
      }
    },
  },
};
</script>

<style scoped>
.carousel {
  overflow: hidden;
}
.carousel-inner {
  display: flex;
  transition-property: transform;
  width: calc(100% * var(--num-slides));
}
.carousel-inner > * {
  flex: 0 0 calc(100% / var(--num-slides));
  width: calc(100% / var(--num-slides));
}
</style>
