<template>
  <button @click="$emit('click')" :disabled="loading || disabled" class="py-2" type="button">
      <div v-show="loading" class="py-2">
         <div class="spinner"></div>
      </div>
   
      <div v-show="!loading" class="flex justify-center">
         <slot name="left_icon"></slot>
            {{btn_name}}
         <slot name="right_icon"></slot>
      </div>
   </button>
</template>

<script>
export default {
   name: 'ButtonUtils',
   props: {
      btn_name: String,
      options: Array,
      loading: Boolean,
      disabled: Boolean,
   },
}
</script>

<style scoped>
button {
   position: relative;  
}

.spinner {
  border: 2px solid rgba(0, 0, 0, 0.1);
  border-left-color: #fff;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -8px;
  margin-left: -8px;
  animation: spinner 0.6s linear infinite;
}
@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}
</style>