<template>
  <div :class="[{'dark-theme' : is_dark, 'light-theme' : !is_dark}]"  id="app" class="app" >

    <div class="sm:container mx-auto relative">
      <NavigationMenu />
      <router-view />
    </div>
    <FooterMenu class="mt-20"/>
  </div>
</template>

<script>
import { computed } from 'vue'
import NavigationMenu from './components/Navbar/NavigationMenu.vue'
import FooterMenu from './components/FooterMenu.vue'

export default {
  name: 'AppView',

  components: {
    NavigationMenu,
    FooterMenu
  },

  data() {
    return {
       page_width: window.innerWidth,
       is_dark: false,
    }
  },

  provide() {
    return {
      is_dark: computed(() => this.is_dark),
      page_width: computed(() => this.page_width),
      toggleTheme: this.toggleTheme
    }
  },

  mounted() {
    this.addResizingListener();
    this.addAnimationListener();
    this.is_dark = JSON.parse(localStorage.getItem('darkTheme')) || false
  },

  

  methods: {
    toggleTheme(value) {
      localStorage.setItem('darkTheme', value)
      return this.is_dark = value
    },

    addResizingListener() {
      return window.addEventListener('resize', this.getPageWidth)
    },

    
    removeResizingListener() {
      return window.removeEventListener('resize', this.getPageWidth)
    },

    getPageWidth() {
      return this.page_width = window.innerWidth;
    },

     animation() {
      const reveals = document.querySelectorAll(".slide-right");

      for (let i = 0; i < reveals.length; i++) {
        let windowHeight = window.innerHeight;
        let elementTop = reveals[i].getBoundingClientRect().top;
        let elementVisible = 150;

        if (elementTop < windowHeight - elementVisible) {
          reveals[i].classList.add("active");
        } else {
          reveals[i].classList.remove("active");
        }
      }
    },

    addAnimationListener() {
      return window.addEventListener('scroll', this.animation)
    },

    removeAnimationListener() {
      return window.removeEventListener('scroll', this.animation)
    },

  },

  beforeDestroy() {
    this.removeResizingListener();
    this.removeAnimationListener();
  }

}
</script>


<style>
.dark-theme {
  background: #202123;
  color: #fff;
  transition-timing-function: ease-in;
  transition: 0.5s;

}

 .landing-animation {
    animation: slideRight 1s ease forwards;
  }

.light-theme {
  background: #fff;
  color: #191624;
  transition-timing-function: ease-in;
  transition: 0.5s;
}

.primary-purple {
  color: #6c56e1;
}

.primary-purple-bg {
  background: #6c56e1 !important;
}

.btn-outlined{
   border: 1px solid #6c56e1;
}

.btn-outlined:hover {
   background: #6c56e1;
   color: #fff;
}

.btn-outlined-dark:hover{
   background: #6c56e1;
}

.app {
  position:relative
}


@keyframes slideRight {
  0% {
     opacity: 0;
     transform: translateX(-100px);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}


</style>

